import { Navigate, useNavigate } from 'react-router-dom';
import { Form, Input, Button, InputNumber, Typography, Divider } from 'antd';
import { SaveOutlined } from '@ant-design/icons';
import { ValidateErrorEntity } from 'rc-field-form/lib/interface';
import { useState } from 'react';
import { getUserStorage } from '../helpers/getUserStorage';
import { useHideMenu } from '../hooks/useHideMenu';

interface AgentsProps {
    agente:string;
    posicion:string
}

const { Title, Text} = Typography;

export const Ingresar = () => {

    useHideMenu( false );
    const navigate = useNavigate();
    const [ user ] = useState( getUserStorage() );


    if( user.agente && user.posicion ){
        return <Navigate to="/escritorio" />
    }

    const onFinish = ({ agente, posicion }:AgentsProps) => {

        localStorage.setItem('agente', agente);
        localStorage.setItem('posicion', posicion);

        navigate("/escritorio")


    }

    const onFinishFailed = (errorInfo: ValidateErrorEntity<AgentsProps>) => {
        console.log('Failed:', errorInfo);
    };



    return (
        <>

            <Title level={ 2 }> Ingresar</Title>
            <Text>Ingrese su nombre y numero de escritorio</Text>
            <Divider />
            <Form
                name="basic"
                labelCol={{
                span: 8,
                }}
                wrapperCol={{
                span: 14,
                }}
                initialValues={{
                remember: true,
                }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                autoComplete="off"
            >
                <Form.Item
                    label="Nombre del Agente"
                    name="agente"
                    rules={[
                        {
                        required: true,
                        message: 'Por favor ingrese su nombre!',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    label="Posicion"
                    name="posicion"
                    rules={[
                        {
                        required: true,
                        message: 'Por favor ingrese el numero de posicion!',
                        },
                    ]}
                >
                    <InputNumber />
                </Form.Item>


                <Form.Item
                    wrapperCol={{
                        offset: 8,
                        span: 16,
                    }}
                >
                <Button 
                    type="primary" 
                    htmlType="submit"
                    shape='round'
                    >
                        <SaveOutlined />
                        Ingresar
                    </Button>
                </Form.Item>
            </Form>
        </>
    )
}
