import { useContext, useEffect } from "react"
import { UiContext } from "../context/ui/uiContext"



export const useHideMenu = ( hide:Boolean ) => {

    const { showMenu, hideMenuAction } = useContext( UiContext );

    useEffect(() => {
        if( hide ){
            hideMenuAction();
        } else {
            showMenu();
        }
    }, [ hide, showMenu, hideMenuAction ])
    

}