import { Col, Row, Typography, List, Card, Tag, Divider } from "antd";
import { useContext, useEffect, useState } from "react";
import { SocketContext } from "../context/socketContext";
import { getUltimos } from "../helpers/getUltimos";
import { useHideMenu } from "../hooks/useHideMenu";
import { TicketProps } from "./CrearTicket";


const { Text, Title } = Typography;

export const Cola = () => {

    useHideMenu( true );
    const { socket } = useContext( SocketContext );
    const [ tickets, setTickets] = useState<TicketProps[]>();


    useEffect(() => {
        socket.on('ticket-asignado', ( asignados:TicketProps[] ) => {
            setTickets( asignados )
        });
        return () => {
            socket.off('ticket-asignado')
        }

    }, [ socket ])
    


    useEffect(() => {
        getUltimos().then( tickets => setTickets( tickets ));
    
    }, [])
    

    return (
        <>
            <Title level={ 1 }>Atendiendo al cliente</Title>
            <Row>
                <Col span={ 12 }>
                    <List 
                        dataSource={ tickets?.slice(0, 3) }
                        renderItem={ item => (
                            <List.Item>
                                <Card
                                    style={{ width: 300, marginTop: 16 }}
                                    actions={[
                                        <Tag color="volcano">{ item.agente }</Tag>,
                                        <Tag color="#42414d">Posicion: { item.posicion }</Tag>,
                                    ]}
                                >
                                    <Title>{ item.tipo} { item.number }</Title>
                                </Card>
                            </List.Item>
                        ) }
                    />
                </Col>

                <Col span={ 12 }>
                    <Divider > Historial </Divider>
                    <List 
                        dataSource={ tickets?.slice(3) }
                        renderItem={ item => (
                            <List.Item>
                                <List.Item.Meta 
                                    title={ `${ item.tipo} ${ item.number } `}
                                    description={
                                        <>
                                            <Text type="secondary">En la posicion </Text>
                                            <Tag color="#42414d">{ item.posicion }</Tag>
                                            <Text type="secondary">Agente: </Text>
                                            <Tag color="volcano">{ item.agente }</Tag>
                                        </>
                                    }
                                />
                            </List.Item>
                        )}
                    />
                </Col>
            </Row>
        </>
    )
}
