import { ArrowRightOutlined, CloseCircleOutlined } from "@ant-design/icons";
import { Navigate, useNavigate } from 'react-router-dom';
import { Button, Col, Divider, Row, Typography } from "antd";
import { useContext, useState } from "react";
import { getUserStorage } from "../helpers/getUserStorage";
import { SocketContext } from "../context/socketContext";
import { TicketProps } from "./CrearTicket";
import { useHideMenu } from "../hooks/useHideMenu";



const { Title, Text } = Typography;

export const Escritorio = () => {


    useHideMenu( false );
    const { socket } = useContext( SocketContext )
    const [ticket, setTicket] = useState<TicketProps>( )
    const [ user ] = useState( getUserStorage() );
    const navigate = useNavigate();

    const handleLogout = () => {
        localStorage.clear();
        navigate("/ingresar");
    }

    const handleNextTicket = () => {
        socket.emit('next-ticket', user, ( ticket:TicketProps ) => {
            setTicket( ticket )
            
        });
    }


    if( !user.agente && !user.posicion){
        return <Navigate to="/ingresar" />
    }

    return (
        <>
            <Row>
                <Col span={ 20 }>
                    <Title level={ 2 }>{ user.agente }</Title>
                    <Text>Usted esta trabajando en el escritorio: </Text>
                    <Text type='success'> { user.posicion } </Text>
                </Col>

                <Col span={ 4 } >
                    <Button 
                        type='primary' 
                        danger
                        onClick={ handleLogout }
                    >
                        <CloseCircleOutlined  />
                        Salir
                    </Button>
                </Col>

            </Row>

            <Divider />

            {
                ticket &&
                (
                    <Row>   
                        <Col>
                            <Text>Esta atendiendo el ticket numero: </Text>
                            <Text 
                                style={{ fontSize: '30px'}} 
                                type='danger'
                            >
                            { ticket.tipo } { ticket.number }
                            </Text>
                    </Col>
                    </Row>
                )
            }


            <Row>
                <Col offset={ 18 } span={ 6 } >
                <Button 
                        type='primary' 
                        
                        shape='round'
                        onClick={ handleNextTicket }
                    >
                        Siguiente 
                        <ArrowRightOutlined  />
                    </Button>
                </Col>
            </Row>
        
        </>
    )
}
