import { Col, Row, Typography, Card, Button, Divider, } from "antd";
import { DownloadOutlined } from "@ant-design/icons";
import { useContext, useState } from "react";
import { SocketContext } from "../context/socketContext";

const { Title, Text } = Typography;  

export interface TicketProps {
    id:string;
    number: number;
    posicion?:string;
    agente?:string;
    tipo?:string;
}

const cardStyles = { fontSize: '30px', backgroundColor: '#000000', color: 'white'};

export const CrearTicket = () => {

    const { socket } = useContext( SocketContext );
    const [ ticket, setTicket ] = useState<TicketProps>( )


    const newTicket = () => {
        const ticketTo = { ticket: 'reparar'};
        socket.emit('solicitar-ticket', ticketTo, ( ticket: TicketProps ) => {
            setTicket( ticket )
        })
    }

    const newTicketCaja = () => {
        const ticketTo = { ticket: 'caja'};
        socket.emit('solicitar-ticket-caja', ticketTo, ( ticket: TicketProps ) => {
            setTicket( ticket )
        
            
        })
    }

    const newTicketServices = () => {
        const ticketTo = { ticket: 'servicios'};
        socket.emit('solicitar-ticket-servicios', ticketTo, ( ticket: TicketProps ) => {
            setTicket( ticket )
            
            
        })
    }

  return (
    <div className="site-card-wrapper">
        <Title level={ 3 }> 
            Presione el boton para generar un ticket
        </Title>
        <Divider />
        <Row gutter={24} justify="center">
            <Col span={8} >
                <Card title="Reparaciones" bordered={true} headStyle={ cardStyles }  className="card-botones-style">
                <Button 
                    type='primary'
                    style={{ background: 'black', borderColor: 'black',}}
                    size='large'
                    block
                    onClick={ newTicket }
                    icon={<DownloadOutlined />}

                >
                    Nuevo Ticket
                </Button>
                </Card>
            </Col>
            <Col span={8}>
                <Card title="Caja" bordered={true} headStyle={ cardStyles }  className="card-botones-style" >
                <Button 
                    type='primary'
                    style={{ background: 'black', borderColor: 'black'}}
                    size='large'
                    onClick={ newTicketCaja }
                    icon={<DownloadOutlined />}
                    block
                >
                    Nuevo Ticket
                </Button>
                </Card>
            </Col>
            <Col span={8}>
                <Card title="Servicio al Cliente" headStyle={ cardStyles }  bordered={true} className="card-botones-style">
                <Button 
                    type='primary'
                    style={{ background: 'black', borderColor: 'black'}}
                    size='large'
                    onClick={ newTicketServices }
                    icon={<DownloadOutlined />}
                    block

                >
                    Nuevo Ticket
                </Button>
                </Card>
            </Col>
        </Row>
        {
            ticket &&
            (
                <Row style={{ marginTop: '100px',}}>
                <Col  span={ 14 } offset={ 6 } >
                    <Text style={{ fontSize: '30px', fontWeight: 'bold', }}>
                        Su numero
                    </Text>
                    <br/>
                    <Text type='success' style={{ fontSize: 55}}>
                       {ticket.tipo} { ticket.number }
                    </Text>
                </Col>
            </Row>
            )

        }

    </div>
  )
}
